/* src/App.css */
.App {
  text-align: center;
}

nav ul {
  list-style-type: none;
  padding: 0;
}

nav ul li {
  display: inline;
  margin-right: 20px;
}

nav ul li a {
  text-decoration: none;
  color: #333;
  font-size: 18px;
}

nav ul li a:hover {
  color: #000;
}

.banner {
  position: relative;
}

.banner img {
  width: 100%;
  height: auto;
}

.banner-text {
  position: absolute;
  top: 100px; /* Или любое другое значение в пикселях */
  left: 50%;
  transform: translate(-50%, -50%);
  color: black;
  text-align: center;
}


.services {
  margin-top: 50px;
}

.services-list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.service {
  width: 30%;
  margin-bottom: 30px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  text-align: left;
}

.service h3 {
  margin-top: 0;
}

.benefits {
  margin-top: 50px;
}

.benefits-list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.benefit {
  width: 30%;
  margin-bottom: 30px;
}

.testimonials {
  margin-top: 50px;
}

.testimonial {
  margin-bottom: 30px;
}

/* Добавляем анимацию при наведении курсора на элементы */
.banner-container img:hover {
  transform: scale(1.05); /* Увеличиваем изображение при наведении */
  transition: transform 0.3s ease; /* Плавное изменение с использованием анимации */
}

.service:hover,
.benefit:hover,
.testimonial:hover {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1); /* Добавляем тень при наведении на сервисы, преимущества и отзывы */
  transition: box-shadow 0.3s ease; /* Плавное изменение тени */
}



